<template>
  <div>
    <div class="content" v-if='isDom'>
      <div class="isDom">
        <div class='msgContent' v-if="type === 'msg'">
          <div v-if="tpl === 1" class=''>
            <div class="overview-box">
              <div class="header"></div>
              <div class="msg-box">
                {{tag}}<span class='webinar-name'>{{webinarName}}</span><span class='date'>{{date}}</span> {{link}}
              </div>
            </div>
          </div>
          <div v-if="tpl === 2" class=''>
            <div class="overview-box">
              <div class="header"></div>
              <div class="msg-box">
                {{tag}}<span class='webinar-name'>{{webinarName}}</span><span class='date'>{{date}}</span> {{link}}
              </div>
            </div>
          </div>
          <div v-if="tpl === 3" class=''>
            <div class="overview-box">
              <div class="header"></div>
              <div class="msg-box">
                {{tag}}<span class='webinar-name'> {{webinarName}} </span>  {{countVal}}  {{link}}
              </div>
            </div>
          </div>
          <div v-if="tpl === 4" class=''>
            <div class="overview-box">
              <div class="header"></div>
              <div class="msg-box">
                {{tag}}<span class='host-name'> {{hostName}} </span>
              </div>
            </div>
          </div>
          <div v-if="tpl === 5" class=''>
            <div class="overview-box">
              <div class="header"></div>
              <div class="msg-box">
                {{tag}}<span class='webinar-name'> {{webinarName}} </span> {{link}}
              </div>
            </div>
          </div>
        </div>
        <div class='wxContent' v-if="type === 'wx'">
          <div v-if="tpl === 1" class=''>
            <div class="overview-box">
              <div class="header"></div>
              <div class="msg-box">
                <p class='title'></p>
                <p><span class='webinar-name'> {{webinarName}} </span></p>
                <p><span class='date'>{{date}}</span></p>
                <p></p>
              </div>
            </div>
          </div>
          <div v-if="tpl === 2" class=''>
            <div class="overview-box">
              <div class="header"></div>
              <div class="msg-box">
                <p class='title'></p>
                <p><span class='webinar-name'>{{webinarName}}</span></p>
                <p><span class='date'>{{date}}</span></p>
                <p></p>
              </div>
            </div>
          </div>
          <div v-if="tpl === 3" class=''>
            <div class="overview-box">
              <div class="header"></div>
              <div class="msg-box">
                <p class='title'> {{countVal}} </p>
                <p><span class='webinar-name'>{{webinarName}}</span></p>
                <p><span class='date'>{{date}}</span></p>
                <p></p>
              </div>
            </div>
          </div>
          <div v-if="tpl === 4" class=''>
            <div class="overview-box">
              <div class="header"></div>
              <div class="msg-box">
                <p class='title'></p>
                <p></p>
                <p>2018-08-31</p>
                <p><span> {{hostName}} </span></p>
              </div>
            </div>
          </div>
          <div v-if="tpl === 5" class=''>
            <div class="overview-box">
              <div class="header"></div>
              <div class="msg-box">
                <p class='title'></p>
                <p><span class='webinar-name'>{{webinarName}}</span></p>
                <p><span class='date'>{{date}}</span></p>
                <p></p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <p class="is-string" v-if='isString'>
      <template v-if="type === 'SMS'">
        <template v-if="tpl === 1">{{tag}} {{webinarName}}{{date}} {{link}}</template>
        <template v-if="tpl === 2">{{tag}} {{webinarName}}{{date}} {{link}}</template>
        <template v-if="tpl === 3">{{tag}} {{webinarName}}   {{countVal}}  {{link}}</template>
        <template v-if="tpl === 4">{{tag}}{{hostName}} </template>
        <template v-if="tpl === 5">{{tag}} {{webinarName}}  {{link}}</template>
      </template>
      <template v-if="type === 'WECHAT'">
        <template v-if="tpl === 1"></template>
        <template v-if="tpl === 2"></template>
        <template v-if="tpl === 3"> {{countVal}} </template>
        <template v-if="tpl === 4"> {{hostName}} </template>
        <template v-if="tpl === 5"></template>
      </template>
    </p>
  </div>
</template>
<script>
  export default {
    name: 'send-tpl',
    data () {
      return {
      }
    },
    props: {
      isDom: {
        type: Boolean,
        default: false
      },
      isString: {
        type: Boolean,
        default: false
      },
      type: { //  msg wx
        type: String,
        default: 'wx'
      },
      triggerType: { // 
        type: String,
        default: ''
      },
      tpl: { // 
        type: Number,
        default: 1
      },
      tag: { // 
        type: String,
        default: ''
      },
      webinarName: { // 
        type: String,
        default: ''
      },
      hostName: { // 
        type: String,
        default: ''
      },
      date: { // 
        type: String,
        default: '2018-08-30 15:43:00'
      },
      firstCount: { //  
        type: Number,
        default: 0
      },
      secondCount: { //  
        type: Number,
        default: 0
      },
      link: { // 
        type: String,
        default: 'www'
      }
    },
    mounted () {
      this.$nextTick(() => {

      })
    },
    computed: {
      countVal () {
        if (this.triggerType === 'BEFORE_HOUR') {
          return `${this.firstCount}`
        }
        if (this.triggerType === 'BEFORE_MINUTE') {
          return `${this.secondCount}`
        }
      }
    }
  }
</script>

